#header {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    height: 14.8vh;
    width: 100vw;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
}

.firstComponent {
    width: 26.7%;
    height: 100%;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}

.firstComponent img {
    width: 126px;
    height: 85px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}

.secondComponent {
    width: 41.3%;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}

.secondComponent ul {
    list-style-type: none;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}

.secondComponent ul :hover {
    color: #a8a8a8;
    -webkit-transition: 0.3s ease-in-out;
    -o-transition: 0.3s ease-in-out;
    transition: 0.3s ease-in-out;
}

.secondComponent a {
    -webkit-transition: 0.3s ease-in-out;
    -o-transition: 0.3s ease-in-out;
    transition: 0.3s ease-in-out;
    margin-right: 10px;
    text-decoration: none;
    color: white;
    font-size: 15px;
    font-weight: 600;
    font-family: 'Arial';
    text-transform: uppercase;
    padding-left: 5px;
    padding-right: 5px;
}

.thirdComponent {
    width: 26.7%;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-left: 3.2%;
}

.thirdComponent p {
    color: white;  
    margin-right: 35px;
    font-family: 'Roboto',Arial,sans-serif;
    font-size: 14px;
    font-weight: 700;
    cursor: pointer;
}

.thirdComponent button {
    color: black;
    height: 34.7%;
    width: 53%;
    background-color:#fff243;
    border: none;
    font-family: 'Roboto',Arial,sans-serif;
    text-align: center;
    white-space: nowrap;
    font-weight: 700;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-size: 14px;
    text-transform: uppercase;
    cursor: pointer;
}

@media all and (max-width: 1200px) {
    #header {
        height: 24vw;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
    }

    .secondComponent {
        width: 0;
        visibility: hidden;
    }

    .thirdComponent {
        width: 0;
        visibility: hidden;
    }
}