#seasonsDiv {
    width: 100%;
    height: 8%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 15px;
    margin-top: 10px;
}

#seasonsDiv p {
    text-align: left;
    font-size: 36px;
    font-family: 'Roboto',Arial,sans-serif;
    font-weight: 600;
    color: #000;
    margin-left: 10%;
    margin-bottom: 0;
}

@media all and (max-width: 1200px) {
    #seasonsDiv {
        height: initial;
        width: initial;
    }

    #seasonsDiv p {
        font-size: 30px;
    }

    #staffCardsDiv p {
        font-size: 30px !important;
        margin-left: 0 !important;
    }

    #staffCardsDiv {
        -webkit-box-pack: center !important;
            -ms-flex-pack: center !important;
                justify-content: center !important;
    }
}