.firstPart {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-flow: column;
            flex-flow: column;
    width: 100%;
    height: 100%;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    background: rgba(255, 255, 255, 1);
    background: -webkit-gradient(left bottom, right top, color-stop(0%, rgba(255, 255, 255, 1)), color-stop(50%, rgba(255, 207, 77, 1)), color-stop(50%, rgba(254, 235, 185, 1)), color-stop(100%, rgba(255, 255, 255, 1)));
    background: -webkit-linear-gradient(45deg, rgba(255, 255, 255, 1) 0%, rgba(255, 207, 77, 1) 50%, rgba(254, 235, 185, 1) 50%, rgba(255, 255, 255, 1) 100%);
    background: -o-linear-gradient(45deg, rgba(255, 255, 255, 1) 0%, rgba(255, 207, 77, 1) 50%, rgba(254, 235, 185, 1) 50%, rgba(255, 255, 255, 1) 100%);
    background: linear-gradient(45deg, rgba(255, 255, 255, 1) 0%, rgba(255, 207, 77, 1) 50%, rgba(254, 235, 185, 1) 50%, rgba(255, 255, 255, 1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffff', endColorstr='#ffffff', GradientType=1);
}

form {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-flow: column;
            flex-flow: column;
    width: 25%;
    height: 90%;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}

.firstPart form p {
    margin-top: 0;
    -ms-flex-item-align: start;
        align-self: flex-start;
    width: 200px;
    margin-left: 15%;
    -webkit-font-smoothing: antialiased;
    font-family: 'Roboto', Arial, sans-serif;
    font-weight: 300;
    cursor: default;
    color: #000;
}

.firstPart h2 {
    font-size: 36px;
    margin-bottom: 11px;
}

.firstPart :last-child {
    text-align: center;
    margin-right: 20%;
    margin-left: 20%;
    line-height: 1.55;
    font-size: 15px;
    font-family: 'Roboto', Arial, sans-serif;
    font-weight: 300;
    -webkit-box-shadow: none;
            box-shadow: none;
    margin-bottom: 25px;
    margin-top: 0px;
}

.firstPart :last-child a {
    text-decoration: none;
    color: #ff8562;
    margin: 0;
}

.twoForm {
    width: 70%;
    font-size: 20px;
    height: 35px;
    font-size: 18px;
    margin-bottom: 25px;
    border-radius: 4px;
    padding-left: 5px;
}

.forms-p-tag {
    font-size: 16px;
    line-height: 1.55;
    text-align: center;
    margin-top: 0px;
    margin-bottom: 26px;
    font-family: 'Roboto', Arial, sans-serif;
    font-weight: 300;
    color: #000;
}

.comments {
    width: 70%;
    height: 70px;
    font-size: 12px;
    margin-bottom: 25px;
    border-radius: 4px;
    font-family: 'Roboto', Arial, sans-serif;
    resize: none;
    padding-left: 5px;
}

.submitButton {
    color: #000;
    width: 70%;
    height: 35px;
    border: none;
    cursor: pointer;
    padding: 0 30px;
    margin: 0;
    border-radius: 5px;
    color: #ffffff;
    background-color: #000000;
}

.submitButton:hover {
    -webkit-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
    background: #fff;
    color: #000;
}

form input:hover {
    -webkit-box-shadow: 0 0 6px 3px rgba(58, 219, 253, 0.35);
            box-shadow: 0 0 6px 3px rgba(58, 219, 253, 0.35);
}

.firstPart p {
    width: 25%;
}

@media all and (max-width:1200px) {
    .firstPart :last-child {
        margin-right: 0 !important;
        margin-left: 0 !important;
        width: initial;
        -ms-flex-item-align: center;
            -ms-grid-row-align: center;
            align-self: center;
    }

    form {
        width: 80%;
        height: initial;
        -webkit-box-align: start;
            -ms-flex-align: start;
                align-items: flex-start;
    }

    form p {
        margin-left: 0 !important;
    }

    .firstPart p {
        width: initial;
    }

    .twoForm {
        width: calc(100% - 10px);
    }

    #textarea {
        width: calc(100% - 10px);
    }
}