.staffCard {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-flow: column;
            flex-flow: column;
    width: 25%;
    height: 85%;
}

.staffCard img {
    height: 80%;
    width: 90%;
}

.staffCard h3 {
    margin-bottom: 10px;
}

.staffCard p {
    margin: 0;
    font-size: 14px;
    line-height: 1.55;
    font-family: 'Roboto',Arial,sans-serif;
    font-weight: 300;
    color: #494949;
}

@media all and (max-width: 1200px) {
    .staffCard {
        width: 220px !important;
        height: 435px;
    }

    .staffCard img {
        height: 305px;
        width: 220px;
    }
}