.closeSideBar {
    width: 100%;
    min-height: 40px;
}

.closeIcon {
    color: #000;
    position: absolute;
    margin-top: 11px;
    margin-left: 0;
    width: 24px;
    height: 24px;
    right: 28px;
}
  
.closeIcon:before {
    content: '';
    position: absolute;
    top: 10px;
    width: 24px;
    height: 2px;
    background-color: currentColor;
    -webkit-transform: rotate(-45deg);
        transform: rotate(-45deg);
}
  
.closeIcon:after {
    content: '';
    position: absolute;
    top: 10px;
    width: 24px;
    height: 2px;
    background-color: currentColor;
    -webkit-transform: rotate(45deg);
        transform: rotate(45deg);
}

.closeIcon:hover {
    cursor: pointer;
}

#sideBarLogoWrapper {
    height: 97.7833px;
    width: 100%;
    display: flex;
    justify-content: center;
    margin-bottom: 25px;
}

#sideBarLogoWrapper img {
    height: inherit;
    width: 220px;
}

#sideBarMenuWrapper {
    width: inherit;
    height: 231px;
    display: flex;
    justify-content: center;
}

#sideBarMenuWrapper ul {
    width: 220px;
    height: inherit;
    list-style-type: none;
    margin: 0px;
    padding: 0;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
}

#sideBarMenuWrapper ul li a {
    text-decoration: none;
    color: #000000;
    font-size: 21px;
    font-family: 'Roboto',Arial,sans-serif;
}

.sideBarButtonsWrapper {
    margin-top: auto;
    height: 183.5px;
    width: inherit;
    display: flex;
    justify-content: center;
}

.sideBarButtons {
    height: 145.5px;
    width: 220px;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
}

.sideBarButtons a {
    width: inherit;
    margin: 0;
}

.socialNetworkWrapper {
    width: inherit;
    height: 36.5px;
    margin-top: 24px;
    display: flex;
}

.socialNetworkWrapper div {
    margin-top: 2px;
}

.instagramIcon {
    margin-left: 5px;
}
#seasonsDiv {
    width: 100%;
    height: 8%;
    display: flex;
    margin-bottom: 15px;
    margin-top: 10px;
}

#seasonsDiv p {
    text-align: left;
    font-size: 36px;
    font-family: 'Roboto',Arial,sans-serif;
    font-weight: 600;
    color: #000;
    margin-left: 10%;
    margin-bottom: 0;
}

@media all and (max-width: 1200px) {
    #seasonsDiv {
        height: auto;
        height: initial;
        width: auto;
        width: initial;
    }

    #seasonsDiv p {
        font-size: 30px;
    }

    #staffCardsDiv p {
        font-size: 30px !important;
        margin-left: 0 !important;
    }

    #staffCardsDiv {
        justify-content: center !important;
    }
}
#staffCardsDiv {
    width: 100%;
    height: 8%;
    display: flex;
}

#staffCardsDiv p {
    text-align: left;
    font-size: 36px;
    font-family: 'Roboto',Arial,sans-serif;
    font-weight: 600;
    color: #000;
    margin-left: 11%;
    margin-bottom: 0;
}
#gallery {
    display: flex;
    justify-content: center;
}

.image-gallery {
    width: 55%;
}

@media all and (max-width: 1200px) {
    .image-gallery {
        width: 100%;
    }
}
