#about {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: inherit;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-flow: column;
            flex-flow: column;
    background-color: #eff4f8;
}

.aboutText {
    width: 70vw;
}

.aboutButton {
    border: none;
    border-radius: 25px;
    width: 7.72%;
    min-width: 125px;
    min-height: 50px;
}

@media all and (max-width: 1200px) {
    .aboutText {
        width: 90vw;
    }
}